@Component({
  selector: 'app-reactivate-my-account',
  templateUrl: './reactivate-my-account.page.html',
  styleUrls: ['./reactivate-my-account.page.scss'],
})
export class ReactivateMyAccountPage extends OmniBasePage implements OnInit {

  public formGroup: FormGroup = new FormGroup({});
  reactivateMyAccountVO = {}
  @ViewChild('usernameRef', { static: false }) usernameRef: PsInputUsernameComponent;

  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'reactivate_my_account_key',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['reactivate_account_step1'],
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.reactivateMyAccountEndPoint,
      group: this.formGroup
    },
    requestObject: this.reactivateMyAccountVO
  };

  panelOptions1Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'identification_details_key',
    iconName: 'reactivate_myAccount_panel1',
    expanded: true,

  };

  panelOptions2Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'authentication_details_key',
    iconName: 'reactivate_myAccount_panel2',
    expanded: true,
  };

  panelOption2Step2: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'change_password_key',
    iconName: 'document',
    expanded: false
  };
  cifNumOptions: IOptionsPsInputNumericExposed = {
    labelKey: 'cif_no_key',
    group: this.formGroup,
    placeHolder: 'enter_cif_no_key',
    fcName: 'userCifNo'
  }

  userNameOptions: IOptionsPsInputUserNameExposed = {
    labelKey: 'username_key',
    group: this.formGroup,
    placeHolder: 'enter_username_key',
    fcName: 'userName'
  }

  accountNumberOptions: IOptionsPsInputAccountNumberExposed = {
    labelKey: 'account_number_key',
    group: this.formGroup,
    placeHolder: 'enter_account_number_key',
    fcName: 'accountNumber',
    requestObject: this.reactivateMyAccountVO
  }
  cardNumberOptions: IOptionsPsInputCardNumberExposed = {
    labelKey: 'card_number_key',
    group: this.formGroup,
    placeHolder: 'enter_card_number_key',
    fcName: 'cardNumber'
  }

  cardPinOptions: IOptionsPsInputPassword = {
    fcName: 'cardPin',
    labelKey: 'card_pin_key',
    placeHolder: 'enter_card_pin_key',
    group: this.formGroup
  };

  complexIdDetailsOptions: IOptionsPsComplexIdDetailsExposed = {
    dropdownIdTypesOptions: {
      fcName: 'idType',
      group: this.formGroup,
    },
    idNumberOptions: {
      labelKey: 'id_number_key',
      placeHolder: 'enter_id_number_key',
      fcName: 'idNumber',
      group: this.formGroup
    },
    dateExpiryOptions: {
      labelKey: 'expiry_date_key',
      placeHolder: 'enter_expiry_date_key',
      fcName: 'expiryDate',
      group: this.formGroup,
    },
    countriesOptions: {
      labelKey: 'country_key',
      placeHolder: 'enter_country_key',
      fcName: 'country',
      group: this.formGroup,
    },
    issueDateOptions: {
      labelKey: 'issue_date_key',
      placeHolder: 'issue_date_key',
      fcName: 'issueDate',
      group: this.formGroup,
    }
  };

  mobileNumberOptions: IOptionsPsEntityPhoneNumberExposed = {
    labelKey: 'mobile_key',
    fcName: 'omniUserCO.omniUserVO.MOBILE_NUMBER',
    group: this.formGroup
  }

  psDateDayMonthYearPastOptions: IOptionsPsDateDayMonthYearPastExposed = {
    fcName: 'dateOfBirthday',
    group: this.formGroup,
    labelKey: 'date_of_birth_key',
    placeHolder: 'enter_your_date_of_birth_key'
  };

  confirmPasswordOptions: IOptionsPasswordConfirmExposed = {
    group: this.formGroup,
    password: {
      labelKey: 'new_password_key',
      placeHolder: 'enter_new_password_key',
      fcName: 'newPassword',
      group: this.formGroup,
    },
    confirmPassword: {
      labelKey: 'confirm_password_key',
      placeHolder: 'confirm_new_password_key',
      fcName: 'confirmPassword',
      group: this.formGroup,
    },
    requestObject: this.reactivateMyAccountVO
  };
  isInvalidLoginBlock: boolean;

  constructor(
    private common: PsCommonService,
    public logger: LoggerService,
    private omniCommon: OmniCommonService) {
    super()
  }

  ngOnInit() {
  }

  public async validateUsername(event) {
    //call the service to validate the username is exsits and blocked
    if (event && event.newValue) {
      const paramData: any = {
        userName: event.newValue,
      };
      CommonUtils.presentLoading();
      this.common.http.commonRequestAjax(PsCommonBusinessSettings.serviceUrl.omniUserInfoEndPoint, paramData
      ).then
        (async (result) => {
          const list: Array<any> = result.data.gridModel as Array<any>;
          if (list && list.length > 0) {
            if (list[0].status && list[0].status == CommonBussinessConstant.STATUS_BLOCKED) {
              if (list[0].blockReason && CommonBussinessConstant.REACTIVATE_ACCOUNT_VALID_BLOCK_REASONS.includes(list[0].blockReason)) {
                let isInvalidLoginBlockReason = list[0].blockReason && list[0].blockReason == CommonBussinessConstant.BLOCK_REASON_INVALID_LOGIN;

                if (isInvalidLoginBlockReason)
                  this.isInvalidLoginBlock = true;
                else
                  this.isInvalidLoginBlock = false;
              }
              else {
                const errMsg = await this.omniCommon.checkLabelTrans('invalid_block_reason_key');
                CommonUtils.presentFailureAlert(errMsg);
                this.commonProv.setValInsideNestedObj(this.userNameOptions.fcName, "", this.stepperOptions.group.controls.formData?.value);
              }
            }
            else {
              const errMsg = await this.omniCommon.checkLabelTrans('invalid_user_status_key');
              CommonUtils.presentFailureAlert(errMsg);
              this.commonProv.setValInsideNestedObj(this.userNameOptions.fcName, "", this.stepperOptions.group.controls.formData?.value);
            }
          }
          else {
            const errMsg = await this.omniCommon.checkLabelTrans('invalid_username_key');
            CommonUtils.presentFailureAlert(errMsg);
            this.commonProv.setValInsideNestedObj(this.userNameOptions.fcName, "", this.stepperOptions.group.controls.formData?.value);
          }
          CommonUtils.dismissLoading();
        }).catch((error) => {
          CommonUtils.dismissLoading();
          this.logger.log(error);
          this.commonProv.setValInsideNestedObj(this.userNameOptions.fcName, "", this.stepperOptions.group.controls.formData?.value);
        });
    }
  }

}
